export default function Timeline() {
    const containerRef = useRef(null);
    const { scrollYProgress } = useScroll({
        target: containerRef,
        offset: ["start center", "end center"],
    });

    const scaleY = useSpring(scrollYProgress, {
        stiffness: 100,
        damping: 30,
        restDelta: 0.001
    });

    return (
        <section ref={containerRef} className="relative py-24 px-4 overflow-hidden bg-[#faf8f6]">
            {/* Background Pattern: Paisley Watermark */}
            <div className="absolute inset-0 pointer-events-none opacity-[0.04] bg-[url('https://www.transparenttextures.com/patterns/natural-paper.png')]" />

            {/* Floating Marigold Petals */}
            <FloatingPetals />

            <div className="max-w-6xl mx-auto relative z-10">
                <motion.div
                    initial={{ opacity: 0, y: 20 }}
                    whileInView={{ opacity: 1, y: 0 }}
                    viewport={{ once: true }}
                    className="text-center mb-20"
                >
                    <span className="font-accent text-royal-gold uppercase tracking-[0.3em] text-sm mb-4 block">Our Journey</span>
                    <h2 className="font-script text-6xl text-charcoal mb-4">Our Love Story</h2>
                    <div className="h-px w-24 bg-royal-gold/40 mx-auto" />
                </motion.div>

                <div className="relative">
                    {/* Vertical Line */}
                    <motion.div
                        style={{ scaleY, transformOrigin: "top" }}
                        className="absolute left-1/2 top-0 h-full w-0.5 bg-royal-gold/20 -translate-x-1/2 hidden md:block"
                    />

                    <div className="space-y-24">
                        {milestones.map((item, index) => (
                            <React.Fragment key={index}>
                                <TimelineItem item={item} index={index} isFeatured={index === milestones.length - 1} />
                                {index < milestones.length - 1 && (
                                    <div className="hidden md:flex justify-center my-8 opacity-20 text-royal-gold">
                                        ‚îÅ‚îÅ‚îÅ ‚ù¶ ‚îÅ‚îÅ‚îÅ
                                    </div>
                                )}
                            </React.Fragment>
                        ))}
                    </div>
                </div>
            </div>
        </section>
    );
}

